html, body {
  font-family: 'Roboto', sans-serif;
  padding: 0;
  margin: 0;
}

#root {
  height: 100vh;
}

* {
  box-sizing: border-box;
}
.logo_gc{
  width: 19rem;
  height: 5rem;
}
.joinOuterContainer {
  display: flex;
  justify-content: center;
  text-align: center;
  height: 100vh;
  align-items: center;
  background-color: #343a40;
}

.joinInnerContainer {
  width: 40%;
}

.joinInput {
  border-radius: 0;
  padding: 15px 20px;
  width: 100%;
}

.heading {
  color: white;
  font-size: 2.5em;
  padding-bottom: 10px;
  border-bottom: 2px solid white;
}

.button {
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #2979FF;
    padding: 20px;
    border-radius: 5px;
    display: inline-block;
    border: none;
    width: 100%;
}

.mt-20 {
  margin-top: 20px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .joinOuterContainer {
    height: 100%;
  }

  .joinInnerContainer {
    margin: 1rem;
    width: 90%;
  }


}

button:focus {
  outline: 0;
}

.input-box{
  border: 1px solid rgba(219,219,219,1) !important;
  border: 1px solid rgba(var(--ca6,219,219,219),1) !important;
  border-radius: 3px !important;
  box-sizing: border-box !important;
  align-items: center !important;
  -webkit-box-align: center !important;
  flex-direction: row !important;
  font-size: 14px !important;
  position: relative !important;
  width: 100% !important;
  text-indent: 10px;
}

.input-field input[type=text]:focus {
     border-bottom: 1px !important;
     box-shadow: 0 1px 0 0 !important;
}


.outerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #1A1A1D;
}

.outer{
  display: flex;
  justify-content: center;
  background-color: #1A1A1D;
  padding-top: 1%;
  height:100vh;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #FFFFFF;
  border-radius: 8px;
  height: 70%;
  width: 50%;
}


@media (min-width: 320px) and (max-width: 480px) {
  .outerContainer {
    height: 100%;
  }

  .container {
    width: 100%;
    height: 100%;
  }
}
@media (min-width: 320px) and (max-width: 480px) {
  .main{
    margin: 1rem;
  }
 
}
@media (min-width: 480px) and (max-width: 1200px) {
  .container {
    width: 70%;
  }
}

.chat-container{
  display: grid;
  grid:
      'search-container chat-title' 70px
      'conversation-list chat-message-list' 1fr
      'new-message-container chat-form' 78px
      / 170px 1fr;
  min-width: 80%;
  max-width: 90%;
  max-height: 800px;
  width: 100%;
  height: 95vh;
  background: #fff;
  border-radius: 10px;
}

.search-container, .conversation-list, .new-message-container{
  background-color: #8f9fbe;

}

.search-container{
  display: grid;
  align-items: center;
  padding: 0 20px;
  grid-area: search-container;
  border-radius:10px 0 0 0;
  box-shadow: 0 1px 3px -1px rgba(0,0,0,0.75);
  z-index: 1;
}

.search-container{
  align-items: center;
  align-content: center;
  color: rgb(41, 21, 2);
  outline: none;
  border-radius: 2px;
  border: 0;
 padding: 0.2rem;
}
.search-container img{
  width: 4rem;
  border: 2%;
  
}

.conversation-list{
  grid-area: conversation-list;
}

.new-message-container{
  border-top: 1px solid rgba(0,0,0,0.25);
  border-radius:0 0 0 10px;
}

.chat-title, .chat-form{
  background-color: rgb(161, 173, 177);
}

.chat-title{
  display: grid;
  grid: 36px / 1fr 36px;
  align-content: center;
  align-items: center;
  grid-area: chat-title;
  color: #0048AA;
  font-weight: bold;
  font-size: 0.2rem;
  border-radius: 0 10px 0 0;
  box-shadow: 0 1px 3px -1px rgba(0,0,0,0.75);
  padding: 0 ;
}

.chat-message-list{
  grid-area: chat-message-list;
  overflow-y:hidden;
}

.chat-form{
  grid-area:chat-form;
  border-radius: 0 0 10px 0;
  border-top: 1px solid rgba(0,0,0,0.25);
  border-bottom: 1px solid rgba(0,0,0,0.25);
}

.logo{
  width: 3rem;
  height: 20%;
  padding-right: 4%;
}


.infoBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 4px 4px 0 0;
  height: 60px;
  width: 100%;
}


.leftInnerContainer {
  flex: 0.5 1;
  display: flex;
  align-items: center;
  margin-left: 2%;
  color: black;
  font-size: 1.4rem;
}

.rightInnerContainer {
  display: flex;
  flex: 0.5 1;
  justify-content: flex-end;
}

.onlineIcon {
  margin-right: 5%;
}

.close-icon{
  width: 10%;
}

.form {
  display: flex;
}

.input {
  border: none;
  border-radius: 0;
  padding-left: 5%;
  width: 80%;
  height: 77px;
  font-size: 1.2em;
}

input:focus, textarea:focus, select:focus{
  outline: none;
}

.sendButton {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #2979FF;

  display: inline-block;
  border: none;
  width: 20%;
  height: 77px;
  border-bottom-right-radius: 8px;
  font-size: 1.0rem;
}

.send-icon{
  width: 15%;
  padding-left: 5%;
}

.messageBox {
  background: #F3F3F3;
  border-radius: 20px;
  padding: 5px 20px;
  color: white;
  display: inline-block;
  max-width: 80%;
}

.messageText {
  width: 100%;
  letter-spacing: 0;
  float: left;
  font-size: 1.1em;
  word-wrap: break-word;
}

.messageText img {
  vertical-align: middle;
}

.messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0 5%;
  margin-top: 3px;
}

.sentText {
  display: flex;
  align-items: center;
  font-family: Helvetica;
  color: #828282;
  letter-spacing: 0.3px;
}

.pl-10 {
  padding-left: 10px;
}

.pr-10 {
  padding-right: 10px;
}

.justifyStart {
  justify-content: flex-start;
}

.justifyEnd {
  justify-content: flex-end;
}

.colorWhite {
  color: white;
}

.colorDark {
  color: #353535;
}

.backgroundBlue {
  background: #2979FF;
}

.backgroundLight {
  background: #F3F3F3;
}

.messages {
  padding: 5% 0;
  overflow: auto;
  flex: auto;
}

