.user-container{
  color: #eee;
  padding-left: 5%;
}

.activeContainer {
  display: flex;
  align-items: center;
  margin-bottom: 50%;
  font-size: 1.2rem;
}

.activeItem {
  display: flex;
  align-items: center;
}

.activeContainer img {
  padding-left: 10px;
}

.textContainer h1 {
  margin-bottom: 0px;
}

@media (min-width: 320px) and (max-width: 1200px) {
  .textContainer {
    display: none;
  }
}

